body {
  padding: 0;
  margin: 0 0;
  font-family: sans-serif;
}

.TopNav {
  background: #0078d4;
  display: flex;
  justify-content: space-around;
  color: white;
  padding: 20px;
  box-shadow: 0px 7px 46px -10px rgba(0,0,0,0.75);
}

body {
  font-size: 24px;
}

.header{
	background-color: #ccc;
	position: relative;
	width: 100%;
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold;
	padding: 90px 0 20px 0; 
}

h2{
	text-align: center;
	font-size: 25px;
	padding-bottom: 40px;
}

.card {
    width: 200px;
    height: 150px;
    margin: 15px 45px 115px 45px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
    cursor: pointer;
  }
  
.footer{
	background-color: #490549;
	width: 100%;
	position: relative;
}

.footer p{
	color: white;
	text-align: center;
	padding: 10px 0;
	font-size: 15px;
}
.wrapper {
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 25px 0 50px;
  justify-content: space-around;
  align-content: flex-start;
}

